const API_BASE_URL = 'http://localhost:8000';

export interface LiveMetrics {
  relationship_level: number;
  trust_level: number;
  emotional_sync: number;
  memory_depth: number;
  current_emotion: string;
  current_topic: string;
  insights_count: number;
  conversation_turns: number;
  recent_insights: string[];
  conversation_active: boolean;
  last_updated: string;
}

export interface UserProfile {
  user_id: string;
  created_at: string;
  total_conversations: number;
  avg_relationship_level: number;
  avg_trust_level: number;
  avg_emotional_sync: number;
  dominant_emotions: string[];
  frequent_topics: string[];
  communication_style: string;
  vulnerability_pattern: number;
  personality_traits: Record<string, any>;
  last_active: string;
}

export interface Conversation {
  conversation_id: string;
  user_id: string;
  started_at: string;
  ended_at: string;
  total_turns: number;
  final_relationship_level: number;
  final_trust_level: number;
  final_emotional_sync: number;
  final_memory_depth: number;
  dominant_topic: string;
  emotional_journey: string[];
  conversation_summary: string;
  key_revelations: string[];
  vulnerability_score: number;
}

export interface Insight {
  insight_id: string;
  user_id: string;
  conversation_id: string;
  speech_id: string;
  insight_text: string;
  insight_type: string;
  confidence_score: number;
  timestamp: string;
  supporting_evidence: string[];
  psychological_category: string;
}

export class AuroraAPI {
  private baseUrl: string;

  constructor(baseUrl = API_BASE_URL) {
    this.baseUrl = baseUrl;
  }

  async getLiveMetrics(): Promise<LiveMetrics> {
    const response = await fetch(`${this.baseUrl}/api/metrics`);
    if (!response.ok) {
      throw new Error(`Failed to fetch metrics: ${response.statusText}`);
    }
    return response.json();
  }

  async getUserProfile(userId = 'default_user'): Promise<{
    profile: UserProfile;
    conversation_count: number;
    insights_count: number;
    recent_conversations: Conversation[];
    recent_insights: Insight[];
  }> {
    const response = await fetch(`${this.baseUrl}/api/users/${userId}`);
    if (!response.ok) {
      throw new Error(`Failed to fetch user profile: ${response.statusText}`);
    }
    return response.json();
  }

  async getUserInsights(userId = 'default_user', limit = 20): Promise<{
    user_id: string;
    total_insights: number;
    insights_by_category: Record<string, Insight[]>;
    all_insights: Insight[];
  }> {
    const response = await fetch(`${this.baseUrl}/api/users/${userId}/insights?limit=${limit}`);
    if (!response.ok) {
      throw new Error(`Failed to fetch user insights: ${response.statusText}`);
    }
    return response.json();
  }

  async getUserAnalytics(userId = 'default_user'): Promise<{
    user_id: string;
    conversation_analytics: {
      total_conversations: number;
      avg_relationship_level: number;
      avg_trust_level: number;
      avg_emotional_sync: number;
      avg_memory_depth: number;
      avg_vulnerability_score: number;
      relationship_progression: number[];
      trust_progression: number[];
    };
    insight_analytics: {
      total_insights: number;
      insights_by_type: Record<string, number>;
      insights_by_category: Record<string, number>;
      avg_confidence: number;
    };
    behavioral_patterns: {
      dominant_topics: Record<string, number>;
      emotional_patterns: string[][];
      conversation_lengths: number[];
    };
  }> {
    const response = await fetch(`${this.baseUrl}/api/analytics/user/${userId}`);
    if (!response.ok) {
      throw new Error(`Failed to fetch analytics: ${response.statusText}`);
    }
    return response.json();
  }

  async searchInsights(query: string, userId?: string, limit = 10): Promise<{
    query: string;
    user_id?: string;
    results_count: number;
    insights: Insight[];
  }> {
    const body = { query, limit, ...(userId && { user_id: userId }) };
    const response = await fetch(`${this.baseUrl}/api/insights/search`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(body)
    });
    if (!response.ok) {
      throw new Error(`Failed to search insights: ${response.statusText}`);
    }
    return response.json();
  }

  async processSpeech(text: string, userId = 'default_user', conversationId?: string): Promise<{
    speech_record: any;
    updated_metrics: LiveMetrics;
    user_profile: UserProfile;
    processing_status: string;
    database_stored: boolean;
  }> {
    const body = {
      text,
      user_id: userId,
      ...(conversationId && { conversation_id: conversationId })
    };

    const response = await fetch(`${this.baseUrl}/api/process-speech`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(body)
    });
    if (!response.ok) {
      throw new Error(`Failed to process speech: ${response.statusText}`);
    }
    return response.json();
  }

  async createConversation(): Promise<{
    conversation_id: string;
    conversation_url: string;
    persona_id: string;
    webhook_url?: string;
    html_client_needed: boolean;
    instructions: string;
  }> {
    const response = await fetch(`${this.baseUrl}/api/create-conversation`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' }
    });
    if (!response.ok) {
      throw new Error(`Failed to create conversation: ${response.statusText}`);
    }
    return response.json();
  }

  async getDatabaseStatus(): Promise<{
    database_connected: boolean;
    tables_initialized: {
      users: boolean;
      conversations: boolean;
      insights: boolean;
    };
    database_path: string;
    record_counts?: {
      users: number;
      conversations: number;
      insights: number;
      total_records: number;
    };
  }> {
    const response = await fetch(`${this.baseUrl}/api/database/status`);
    if (!response.ok) {
      throw new Error(`Failed to fetch database status: ${response.statusText}`);
    }
    return response.json();
  }

  async getRecentActivity(): Promise<{
    recent_users?: Array<{
      user_id: string;
      last_active: string;
      total_conversations: number;
    }>;
    recent_conversations?: Array<{
      conversation_id: string;
      user_id: string;
      ended_at: string;
      total_turns: number;
    }>;
    recent_insights?: Array<{
      insight_text: string;
      user_id: string;
      timestamp: string;
      insight_type: string;
    }>;
  }> {
    const response = await fetch(`${this.baseUrl}/api/database/recent-activity`);
    if (!response.ok) {
      throw new Error(`Failed to fetch recent activity: ${response.statusText}`);
    }
    return response.json();
  }

  async resetSystem(): Promise<{
    status: string;
    metrics: LiveMetrics;
  }> {
    const response = await fetch(`${this.baseUrl}/api/reset`, {
      method: 'DELETE'
    });
    if (!response.ok) {
      throw new Error(`Failed to reset system: ${response.statusText}`);
    }
    return response.json();
  }

  async getAllSpeeches(): Promise<{
    total_speeches: number;
    speeches: any[];
    current_metrics: LiveMetrics;
  }> {
    const response = await fetch(`${this.baseUrl}/api/speeches`);
    if (!response.ok) {
      throw new Error(`Failed to fetch speeches: ${response.statusText}`);
    }
    return response.json();
  }
}

export const auroraAPI = new AuroraAPI();